<script>
    import Swiper from 'swiper';
    import company from '@/components/company.vue';

    export default {
        components : {
            company
        },
        data () {
            return {
                mySwiper: null,
                current_swiper_index: 0,
                // 更多分类弹窗
                show_more_category: false,
                // 弹窗高度
                more_category_height: 0,
                modal_animation: false
            }
        },
        mounted () {
            let that = this;
            // 实例swiper
            this.mySwiper = new Swiper ('.swiper-container', {
                on:{
                    transitionEnd: function(){
                    that.current_swiper_index = this.realIndex;
                    }
                }
            });
            // ‘全部’的轮播
            this.myInnerSwiper = new Swiper ('.swiper-container-inner', {
                pagination: {
                    el: '.swiper-pagination',
                    dynamicBullets: true,
                },
                on:{
                    transitionEnd: function(){
                    // that.current_swiper_index = this.realIndex;
                    }
                }
            });
            // 弹窗全屏高度
            let page_height = window.screen.height;
            this.more_category_height = page_height + 'px';

            // 监听动画结束再隐藏
            this.$refs.modal.addEventListener('webkitAnimationEnd',function () {
                if(!that.modal_animation) {
                    that.show_more_category = false;
                }
            })
        },
        methods: {
            changeTab (new_index) {
                this.current_swiper_index = new_index;
                // 调用swiper滚动的方法
                this.mySwiper.slideTo(new_index, 400, false);
            },
            showMoreCategory () {
                this.modal_animation = true;
                this.show_more_category = true;
            },
            closeCategoryModal () {
                // 可以优化成动画完成后hide
                this.modal_animation = false;
            }
        }
    }
</script>

